import React, { useState } from "react";
import { gen16CodeId, getSvgSymbols } from "../stores/Utils";

import ReactTooltip from "react-tooltip";

export default function Icon2({
	name,
	size,
	color,
	tip,
	tipMessage,
	backgroundColorTooltip,
	textColorTooltip,
}) {
	let _name = name;
	const [iconId] = useState(gen16CodeId());
	const [show, showTooltip] = useState(true);
	const _dList = getSvgSymbols();
	const _d = _dList && _dList[_name];
	let sZ = size ? size : 24;
	let _color = color ? color : "#5c5c5c";

	return (
		<>
			<svg
				fill={_color}
				height={sZ}
				viewBox="0 0 24 24"
				width={sZ}
				data-for={`_Icons_${iconId}_`}
				data-tip
				onMouseLeave={() => {
					if (tip) {
						showTooltip(false);
						setTimeout(() => showTooltip(true), 50);
					}
				}}>
				<path d={_d ? _d : ""} />
			</svg>
			{tip && show && (
				<ReactTooltip
					id={`_Icons_${iconId}_`}
					effect="solid"
					{...(textColorTooltip && { textColor: textColorTooltip })}
					{...(backgroundColorTooltip && {
						backgroundColor: backgroundColorTooltip,
					})}>
					{tipMessage}
				</ReactTooltip>
			)}
		</>
	);
}
